import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components:{
		uniNavBar,
	},
	data() {
		return {
			datas:{},
			navBarTitle: '给油脂标准详情',
			leftIcon: 'iconfanhui',
			rightIcon: '',
			exitStr: '',
			btnDisabled: true
		}
	},
	onLoad(option) {
		this.datas = JSON.parse(option.item)
		
	},
	methods: {
		toXiangci(){
			
			uni.navigateTo({
				
				url:'xiangci?item='+JSON.stringify(this.datas)
			})
		},
		leftNavTop: () => {
			uni.navigateBack()
		},
		rightNavTop: () => {
			
		}
	}
}
